* {
    padding: 0;
    list-style: none;
    box-sizing: border-box;
    margin: 0;
    scroll-behavior: smooth;
}

nav ul {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    /* OR baseline */
    width: 80%;
    margin: 2.5% auto;
    z-index: 5;
}

nav ul li {
    text-transform: uppercase;
    font-family: 'Courier New', Courier, monospace;
    cursor: pointer;
    border-bottom: 3px solid transparent;
}

nav ul li:hover {
    border-bottom: 3px solid;
    border-image: linear-gradient(to right, green 33%, white 33%, white 66%, red 66%, red 99%, red 100%) 3;
}

nav ul p {
    font-size: 2rem;
    cursor: pointer;
}

.main__logo {
    object-fit: cover;
    margin-right: 5%;
    cursor: pointer;
}

.pasta {
    text-align: center;
    margin: 15% auto 10%;
    font-size: 17rem;
    font-weight: 400;
    letter-spacing: 70px;
}

.pasta span {
    visibility: hidden;
}

.fork__image {
    background-image: url('/public/widelec.png');
    height: 90%;
    width: 50vw;
    position: absolute;
    bottom: 0;
    left: 22.25%;
    background-size: contain;
    background-position: center center;
    background-repeat: no-repeat;
}

.toscany {
    margin: 15% 0 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    background-color: #383B27;
    color: #fff;
}

.toscany aside {
    width: 50%;
    height: 80vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.toscany aside h2 {
    font-size: 2.85rem;
    width: 60%;
    text-align: left;
    margin-left: 12.5%;
    font-weight: 400;
}

.toscany aside p {
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    width: 40%;
    margin-top: 5%;
    margin-left: 12.5%;
    font-size: 1.18rem;
    line-height: 1.65rem;
    word-spacing: 2px;
    letter-spacing: 0.5px;
}

.toscany aside ul {
    font-family: 'Poppins', sans-serif;
    font-weight: 200;
    width: 50%;
    margin: 7.5% auto 0;
}

.toscany aside ul li {
    word-spacing: 2px;
    letter-spacing: 0.5px;
    padding-left: 5px;
    margin-bottom: 5px;
    list-style-type: circle;
    font-size: 1.18rem;
}

.toscany aside ul li:first-of-type {
    list-style-type: disc;
}

.toscany img {
    width: 56.5%;
    object-fit: contain;
    z-index: 5;
    margin-top: -4%;
    margin-bottom: 4%;
}

.references .description {
    font-size: 2.75rem;
    text-align: center;
    margin-bottom: 5%;
}

.references .italian__food {
    color: #685819;
}

.references__section {
    width: 85%;
    display: flex;
    flex-direction: row;
    margin: 0 auto;
}

.bar__background__1 {
    width: 100%;
    background-color: #3d3f2b;
    height: 45px;
    margin-top: 255px;
    position: relative;
    z-index: -5;
}

.bar__background__2 {
    width: 100%;
    background-color: #8F835D;
    height: 45px;
}

.reference__photo__1 {
    background-image: url('/public/vegetables1.png');
    width: 100%;
    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    margin-top: -250px;
}

.reference__photo__2 {
    background-image: url('https://images.unsplash.com/photo-1532460734809-e7f8475ca917?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1437&q=80');
    width: 100%;
    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

.reference__photo__3 {
    background-image: url('https://images.unsplash.com/photo-1541528575606-85b8f8e6746b?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80');
    width: 100%;
    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: auto;
    position: relative;
    z-index: -10;
}

.captions {
    display: flex;
    flex-direction: row;
    width: 85%;
    margin: 0 auto;
}

.captions p {
    margin: 3% 0 5%;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    font-size: 1.5rem;
    width: 33.3333%;
    text-align: center;
}

.company__story {
    margin-top: 12%;
    margin-bottom: 10%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.company__story__description h3 {
    font-weight: normal;
    font-size: 3rem;
    margin-left: 9%;
}

.company__story__description p {
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    margin-left: 9%;
    width: 50%;
    word-spacing: 2px;
    letter-spacing: 0.4px;
    line-height: 2.025rem;
    margin-top: 4%;
    font-size: 1.3rem;
}

.cutlery__image {
    margin-top: 12%;
    margin-right: 5%;
    height: 100%;
}

.info__header {
    font-family: 'Playfair Display', serif;
    font-weight: 300;
    text-align: center;
    font-size: 3.5rem;
    font-weight: bold;
    word-spacing: 3px;
    line-height: 4.75rem;
}


/*  MENU  */

.menu {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: 7.5%;
    flex-direction: column;
}

.menu article {
    margin-top: 5%;
}

.menu__category {
    font-size: 2rem;
    font-weight: normal;
    margin: 2% 0 7.5%;
}

.menu__position {
    width: 37.5%;
    margin: 0 auto 3.25%;
    border-top: 2px solid lightgray;
}

.menu__position:last-of-type {
    padding-bottom: 5%;
    border-bottom: 2px solid lightgray;
}

.menu__position__info {
    margin: 5.5% auto 3%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    font-family: 'Faustina', serif;
}

.meal__name {
    font-weight: 500;
    font-size: 2rem;
    word-spacing: 2px;
    letter-spacing: 0.15px;
}

.meal__price {
    font-size: 1.45rem;
}

.menu__position__description {
    font-family: sans-serif;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    text-align: left;
    width: 60%;
    letter-spacing: 0.7px;
    line-height: 1.65rem;
    font-size: 1.25rem;
    /*font-variant: small-caps;*/
    line-height: 2rem;
    font-size: 1.375rem;
}

.menu__image__1 {
    background-image: url('/public/sideFood1.jpg');
    width: 25vw;
    height: 100vh;
    position: absolute;
    left: -0.5%;
    z-index: -5;
    background-position: right;
    background-size: cover;
    overflow: hidden;
}

.menu__image__2 {
    background-image: url('/public/sideFood2.jpg');
    width: 25vw;
    height: 100vh;
    position: absolute;
    right: 0;
    z-index: -5;
    background-position: center;
    background-size: cover;
    overflow: hidden;
}

.menu__image__3 {
    background-image: url('/public/sideFood3.jpg');
    width: 25vw;
    height: 100vh;
    position: absolute;
    left: -0.5%;
    z-index: -5;
    background-position: center;
    background-size: cover;
    overflow: hidden;
}

.menu__image__4 {
    background-image: url('/public/sideFood4.jpg');
    width: 25vw;
    height: 100vh;
    position: absolute;
    right: 0;
    z-index: -5;
    background-position: center;
    background-size: cover;
    overflow: hidden;
}

.reservation__button {
    width: 15%;
    padding: 1rem 2rem;
    font-size: 1.15rem;
    outline: none;
    text-transform: uppercase;
    margin: 0 auto 5%;
    cursor: pointer;
    transition: .25s ease;
    color: black;
    background-color: #fff;
    border: 3px solid #000;
}

.reservation__button:hover {
    background-color: #000;
    color: #fff;
}

.about__restaurant__image {
    background-image: url('https://images.unsplash.com/photo-1552598715-7eeb9232a2ac?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=100');
    width: 100%;
    height: 87.5vh;
    background-size: cover;
    background-position: center;
}

.about__restaurant__info {
    color: #fff;
    font-size: 4rem;
    position: relative;
    left: 5%;
    top: 40%;
    text-align: center;
    width: 40%;
    font-family: 'Playfair Display', serif;
    letter-spacing: 0.3px;
    word-spacing: 1px;
}

.restaurant__description {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
}

.restaurant__description__image__1 {
    background-image: url('https://images.unsplash.com/photo-1618496147834-e92d66005cca?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=687&q=80');
    width: 25vw;
    height: 90vh;
    position: absolute;
    left: 0%;
    z-index: 5;
    background-position: center;
    background-size: cover;
    overflow: hidden;
    margin-top: -6%;
}

.restaurant__description__image__2 {
    background-image: url('https://images.unsplash.com/photo-1616299908398-9af1134ad522?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=737&q=80');
    width: 27.5vw;
    height: 90vh;
    position: absolute;
    right: 0;
    z-index: 5;
    background-position: right;
    background-size: cover;
    overflow: hidden;
    margin-top: 4.5%;
}

.restaurant__description__text {
    text-align: left;
    width: 37.5%;
    margin: 5% auto 0;
}

.restaurant__description__text h3 {
    font-size: 2.75rem;
    font-weight: normal;
}

.restaurant__description__text p {
    font-size: 1.4rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    margin-top: 7.5%;
    width: 72.5%;
}

.restaurant__description__text p:first-of-type {
    margin-top: 5.5%;
}

.reservation {
    margin-top: 14%;
}

.reservation__callus {
    margin-left: 15rem;
    text-align: left;
}

.reservation__book {
    font-size: 2.75rem;
    font-family: 'Faustina', serif;
}

.reservation__book span {
    color: #685819;
}

.reservation__number {
    font-family: 'Poppins', sans-serif;
    font-size: 2.2rem;
    font-weight: 300;
    margin-top: 1.4rem;
}

.reservation__whatsapp {
    font-family: 'Poppins', sans-serif;
    font-size: 1.35rem;
    font-weight: 300;
    line-height: 1.25rem;
}

.reservation__box {
    border: 1px solid #685819;
    width: 72.5%;
    margin: 5% auto;
}

.reservation__headline {
    font-size: 2.8rem;
    text-align: center;
    margin: 4% auto;
}

.reservation__box button {
    margin: 4% auto 4.5%;
    display: flex;
    padding: 0.95rem 2.6rem;
    text-transform: uppercase;
    outline: none;
    border: 2px solid #685819;
    background-color: #8F835D;
    color: #fff;
    font-size: 1.15rem;
    cursor: pointer;
}

.reservation__box form div {
    width: 85%;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}

.reservation__box form div:nth-of-type(2) {
    margin-top: 5%;
}

input {
    border: none;
    border-bottom: 2px solid grey;
    margin: 1%;
    font-family: 'Poppins', sans-serif;
    font-size: 1.25rem;
    font-weight: 300;
}

input::placeholder {
    color: #000;
}

.reservation__name,
.reservation__phone {
    width: 40%;
}

.reservation__name {
    margin-right: 13.5%;
}

.reservation__guests,
.reservation__date,
.reservation__time {
    width: 30%;
}

footer {
    margin: 12.5% auto 1.25rem;
}

.footer__logo {
    object-fit: cover;
    cursor: pointer;
    display: flex;
    margin: 0 auto;
}